body,
h1 {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

.hitTile {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    padding: 20px;
}

p {
    margin-top: .5rem;
}

p.tiny {
    color: #999999;
}

h3 {
    margin-top: .5rem;
    margin-bottom: .2rem;
}

hr {
    border: 1px solid #EEEEEE;
}

span.featuring {
    font-size: .3rem;
    color: #999999;
}
.tedModalContainer {
    display: flex;
    flex-direction: row;
}

.tedModalImageContainer {
    display: flex;
    flex: 1 1;
    width: 100%;
    background-color: black;
}

.tedModalContent {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    padding: 20px;
}

h1.content {
    font-size: 3rem;
}

p.content {
    font-size: 1.5rem;
}

button {
    float: right;
}
.header {
  display: flex;
  align-items: center;
  min-height: 50px;
  padding: 0.5rem 1rem;
  background-color: black;
  color: #fff;
  margin-bottom: 1rem;
}

.header a {
  color: red;
  font-weight: bold;
  text-decoration: none;
}

.header-title {
  font-size: 1.5rem;
  font-weight: normal;
}

.header-subtitle {
  font-size: .75rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.search-panel {
  display: flex;
}

.search-panel__filters {
  flex: 1 1;
}

.search-panel__results {
  flex: 5 1;
  padding: 10px;
}

.searchbox {
  margin-bottom: 1rem;
}

.statsbox {
  margin-bottom: 2rem;
}

.pagination {
  margin: 2rem auto;
  text-align: center;
}

.left-panel {
  float: left;
}

h3 {
  font-size: 1rem;
  font-weight: bold;
  color: black;
  margin-bottom: 0;
}

p {
  font-size: .85rem;
  margin-bottom: 0;
  padding: 0;
}

li.ais-Hits-item {
  padding: 0;
  margin: 0;
  box-shadow: none;
  border: 3px solid transparent;
  cursor: pointer;
}

li.ais-Hits-item:hover {
  border: 3px solid#CCCCCC;
  box-shadow: none;
  cursor: pointer;
}

p.tiny {
  font-size: .75rem;
}

.search-panel__filters {
  border-right: 1px solid #EEEEEE;
  margin-right: 20px;
}
